// EditStudentModal.js
import React from "react";
import { Modal, TextField, Button, IconButton } from "@mui/material";
import { Close } from "@mui/icons-material";

const EditStudentModal = ({ open, handleClose, selectedStudent, editedStudent, handleChange, handleSave }) => {
  // Validation function for each field
  const validateField = (field, value) => {
    if (field === "studentName" && !/^[a-zA-Z]*$/.test(value)) {
        return "Please Enter a Valid Name(only alphabets are allowed)";
      }
    if (field === "studentName" && value.trim() === "") {
      return "Student name is required";
    }
    if (field === "studentEmail" && value.trim() === "") {
      return "Student email is required";
    }
    if (field === "studentEmail" && !/\S+@\S+\.\S+/.test(value)) {
      return "Invalid email address";
    }
    // Add more validations as needed
    return "";
  };

  // Validation state for each field
  const [validationErrors, setValidationErrors] = React.useState({});

  // Handle change with validation
  const handleChangeWithValidation = (e) => {
    const { name, value } = e.target;
    const error = validateField(name, value);
    setValidationErrors({ ...validationErrors, [name]: error });
    handleChange(e);
  };

  return (
    <Modal open={open} onClose={handleClose}>
      <div
        style={{
          position: "absolute",
          top: "50%",
          left: "50%",
          transform: "translate(-50%, -50%)",
          backgroundColor: "white",
          padding: "20px",
          borderRadius: "5px",
          width: "450px",
        }}
      >
        <h2 id="edit-student-modal">Edit Student</h2>
        <IconButton
          style={{ position: "absolute", top: 15, right: 10 }}
          onClick={handleClose}
        >
          <Close />
        </IconButton>
        <form>
          <TextField
            name="studentName"
            label="Student Name"
            value={editedStudent.studentName}
            onChange={handleChangeWithValidation}
            fullWidth
            error={Boolean(validationErrors.studentName)}
            helperText={validationErrors.studentName}
          />
          <br />
          <br />
          <TextField
            name="studentEmail"
            label="Student Email"
            value={editedStudent.studentEmail}
            onChange={handleChangeWithValidation}
            fullWidth
            error={Boolean(validationErrors.studentEmail)}
            helperText={validationErrors.studentEmail}
          />
          <br />
          <br />
          <TextField
            name="studentAddress"
            label="Student Address"
            value={editedStudent.studentAddress}
            onChange={handleChange}
            fullWidth
          />
          <Button
            variant="contained"
            color="primary"
            onClick={handleSave}
            style={{ margin: "10px" }}
          >
            Save
          </Button>
        </form>
      </div>
    </Modal>
  );
};

export default EditStudentModal;
