import React, { useState } from "react";
import {
  FormContainer,
  TypographyTitle,
  InnerContainer,
  ButtonComponent,
  Text,
} from "./LoginStyles";
import { InputAdornment, IconButton } from "@mui/material";
import { Visibility, VisibilityOff } from "@mui/icons-material";
import { useNavigate } from "react-router-dom";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { Link } from "react-router-dom";

const Signup = () => {
  const [firstname, setFirstname] = useState("");
  const [middlename, setMiddlename] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [showPassword, setShowPassword] = useState(false);
  const navigate = useNavigate();

  const handlePasswordVisibility = () => {
    setShowPassword((prevShowPassword) => !prevShowPassword);
  };

  const handleSubmit = async (e) => {
    e.preventDefault();

    const signupData = {
      firstname,
      middlename,
      email,
      password
    };

    try {
      const response = await fetch("http://localhost:8082/api/signup", {
        method: "POST",
        headers: {
          "Content-Type": "application/json"
        },
        body: JSON.stringify(signupData)
      });

      if (response.ok) {
        toast.success("Signup successful!");
        setTimeout(() => {
          navigate("/");
        }, 2000);
      } else {
        toast.error("Signup failed. Please try again later.");
      }
    } catch (error) {
      console.error("Error signing up:", error);
      toast.error("An error occurred while signing up. Please try again later.");
    }
  };

  return (
    <>
      <FormContainer maxWidth="md" onSubmit={handleSubmit}>
        <TypographyTitle variant="h3">Signup Form</TypographyTitle>
        <InnerContainer>
          <form>
            <Text
              type="text"
              size="small"
              fullWidth
              label="First Name"
              placeholder="Enter your First Name"
              name="firstname"
              value={firstname}
              onChange={(e) => setFirstname(e.target.value)}
              required
            />
            <Text
              type="text"
              size="small"
              fullWidth
              label="Middle Name"
              placeholder="Enter your Middle Name"
              name="middlename"
              value={middlename}
              onChange={(e) => setMiddlename(e.target.value)}
              required
            />
            <Text
              type="email"
              size="small"
              fullWidth
              label="Email Address"
              placeholder="Enter your email address"
              name="email"
              value={email}
              onChange={(e) => setEmail(e.target.value)}
              required
            />
            <Text
              size="small"
              fullWidth
              name="password"
              label="Password"
              placeholder="Enter your password"
              type={showPassword ? "text" : "password"}
              InputProps={{
                endAdornment: (
                  <InputAdornment position="end">
                    <IconButton onClick={handlePasswordVisibility} edge="end">
                      {showPassword ? <VisibilityOff /> : <Visibility />}
                    </IconButton>
                  </InputAdornment>
                ),
              }}
              value={password}
              onChange={(e) => setPassword(e.target.value)}
              required
            />
            <ButtonComponent
              type="submit"
              fullWidth
              style={{ marginTop: "30px" }}
              variant="contained"
              color="primary"
            >
              Submit
            </ButtonComponent>
            <Link to="/">Already have an account? Login here.</Link>
          </form>
        </InnerContainer>
      </FormContainer>
      <ToastContainer />
    </>
  );
};

export default Signup;
