import React, { useState } from "react";
import { Modal, TextField, Button, IconButton } from "@mui/material";
import { Close } from "@mui/icons-material";

const AddStudentModal = ({ open, handleClose, newStudent, handleNewStudentChange, handleAddStudent }) => {
    const validateField = (field, value) => {
        if (field === "studentName" && !/^[a-zA-Z]*$/.test(value)) {
            return "Please Enter a Valid Name(only alphabets are allowed)";
        }
        if (field === "studentName" && value.trim() === "") {
            return "Student name is required";
        }
        if (field === "studentEmail" && value.trim() === "") {
            return "Student email is required";
        }
        if (field === "studentEmail" && !/\S+@\S+\.\S+/.test(value)) {
            return "Invalid email address";
        }
        // Add more validations as needed
        return "";
    };

    const validate = () => {
        const errors = {};
        if (!newStudent || !newStudent.studentName.trim()) {
            errors.studentName = "Please enter a student name";
        }
        if (!newStudent || !newStudent.studentEmail.trim()) {
            errors.studentEmail = "Please enter a student email";
        }
        if (!newStudent || !newStudent.studentAddress || !newStudent.studentAddress.trim()) {
            errors.studentAddress = "Please enter a student address";
        }
        setValidationErrors(errors);
        return Object.keys(errors).length === 0;
    };

    const [validationErrors, setValidationErrors] = useState({});

    const handleChangeWithValidation = (e) => {
        const { name, value } = e.target;
        const error = validateField(name, value);
        setValidationErrors({ ...validationErrors, [name]: error });
        handleNewStudentChange(e);
    };

    const handleAddStudentClick = () => {
        if (validate()) {
            handleAddStudent();
        }
    };

    return (
        <Modal open={open} onClose={handleClose}>
            <div
                style={{
                    position: "absolute",
                    top: "50%",
                    left: "50%",
                    transform: "translate(-50%, -50%)",
                    backgroundColor: "white",
                    padding: "20px",
                    borderRadius: "5px",
                    width: "450px",
                }}
            >
                <h2>Add Student</h2>
                <IconButton
                    style={{ position: "absolute", top: 15, right: 10 }}
                    onClick={handleClose}
                >
                    <Close />
                </IconButton>
                <form>
                    <TextField
                        name="studentName"
                        label="Student Name"
                        value={newStudent ? newStudent.studentName : ''}
                        onChange={handleChangeWithValidation}
                        fullWidth
                        error={Boolean(validationErrors.studentName)}
                        helperText={validationErrors.studentName}
                        required
                    />
                    <br />
                    <br />
                    <TextField
                        name="studentEmail"
                        label="Student Email"
                        value={newStudent ? newStudent.studentEmail : ''}
                        onChange={handleChangeWithValidation}
                        fullWidth
                        error={Boolean(validationErrors.studentEmail)}
                        helperText={validationErrors.studentEmail}
                        required
                    />
                    <br />
                    <br />
                    <TextField
                        name="studentAddress"
                        label="Student Address"
                        value={newStudent && newStudent.studentAddress ? newStudent.studentAddress : ''}
                        onChange={handleNewStudentChange}
                        fullWidth
                        error={Boolean(validationErrors.studentAddress)}
                        helperText={validationErrors.studentAddress}
                        required
                    />
                    <Button
                        variant="contained"
                        color="primary"
                        onClick={handleAddStudentClick}
                        style={{ margin: "10px" }}
                    >
                        Add
                    </Button>
                </form>
            </div>
        </Modal>
    );
};

export default AddStudentModal;
