// StudentList.js
import React, { useState, useEffect } from "react";
import axios from "axios";
import {
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Paper,
  IconButton,
  Button,
} from "@mui/material";
import EditIcon from "@mui/icons-material/Edit";
import EditStudentModal from "./EditStudent";
import AddStudentModal from "./AddStudent";
import { toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css"; 

const StudentList = () => {
  const [students, setStudents] = useState([]);
  const [openEdit, setOpenEdit] = useState(false);
  const [openAdd, setOpenAdd] = useState(false);
  const [selectedStudent, setSelectedStudent] = useState(null);
  const [editedStudent, setEditedStudent] = useState({});
  const [newStudent, setNewStudent] = useState({
    studentName: "",
    studentEmail: "",
    studentAddress: "",
  });

  useEffect(() => {
    const fetchStudents = async () => {
      try {
        const response = await axios.get("http://localhost:8082/api/students");
        setStudents(response.data);
      } catch (error) {
        console.error("Error fetching students:", error);
      }
    };

    fetchStudents();
  }, []);

  const handleEdit = (student) => {
    setSelectedStudent(student);
    setEditedStudent({ ...student });
    setOpenEdit(true);
  };

  const handleCloseEdit = () => {
    setOpenEdit(false);
    // toast.info("Edit modal closed");
  };

  const handleChange = (e) => {
    setEditedStudent({ ...editedStudent, [e.target.name]: e.target.value });
  };

  const handleSave = async () => {
    try {
      await axios.put(
        `http://localhost:8082/api/students/${selectedStudent.id}`,
        editedStudent
      );
      handleCloseEdit();
      const updatedStudents = students.map((student) =>
        student.id === selectedStudent.id ? editedStudent : student
      );
      setStudents(updatedStudents);
      toast.success("Student details updated successfully!");
    } catch (error) {
      console.error("Error updating student:", error);
    }
  };

  const handleAddStudent = async () => {
    if (!newStudent.studentName.trim() || !newStudent.studentEmail.trim() || !newStudent.studentAddress.trim()) {
      toast.error("Please fill all the fields");
      return;
    }
  
    try {
      await axios.post("http://localhost:8082/api/students", newStudent);
      setStudents([...students, newStudent]);
      setNewStudent({ studentName: "", studentEmail: "", studentAddress: "" });
      handleCloseAdd();
      toast.success("Student added successfully!");
    } catch (error) {
      console.error("Error adding student:", error);
    }
  };

  const handleNewStudentChange = (e) => {
    setNewStudent({ ...newStudent, [e.target.name]: e.target.value });
  };

  const handleCloseAdd = () => {
    setOpenAdd(false);
    // toast.info("Add modal closed"); 
  };

  return (
    <div>
      <h1 style={{textAlign:"center"}}>STUDENT LIST</h1>
      <Button
        variant="contained"
        color="primary"
        onClick={() => setOpenAdd(true)}
        style={{ float: "right", margin: "10px" }}
      >
        Add Student
      </Button>
      <TableContainer component={Paper}>
        <Table>
          <TableHead>
            <TableRow style={{ backgroundColor: "#301934" }}>
              <TableCell style={{ color: "white", fontSize: "medium" }}>
                <strong>Student Name</strong>
              </TableCell>
              <TableCell style={{ color: "white", fontSize: "medium" }}>
                <strong>Student Email</strong>
              </TableCell>
              <TableCell style={{ color: "white", fontSize: "medium" }}>
                <strong>Student Address</strong>
              </TableCell>
              <TableCell style={{ color: "white", fontSize: "medium" }}>
                <strong>Edit</strong>
              </TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {students.map((student) => (
              <TableRow key={student.id}>
                <TableCell>{student.studentName}</TableCell>
                <TableCell>{student.studentEmail}</TableCell>
                <TableCell>{student.studentAddress}</TableCell>
                <TableCell>
                  <IconButton onClick={() => handleEdit(student)}>
                    <EditIcon  style={{color:"blue"}}/>
                  </IconButton>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      {/* Edit Student Modal */}
      <EditStudentModal
        open={openEdit}
        handleClose={handleCloseEdit}
        selectedStudent={selectedStudent}
        editedStudent={editedStudent}
        handleChange={handleChange}
        handleSave={handleSave}
      />

      {/* Add Student Modal */}
      <AddStudentModal
        open={openAdd}
        handleClose={handleCloseAdd}
        newStudent={newStudent}
        handleNewStudentChange={handleNewStudentChange}
        handleAddStudent={handleAddStudent}
      />
    </div>
  );
};

export default StudentList;
